<template>
	<div id="app" class="container-fluid">
		<div class="content row">
			<nav>
				<router-link to="/addacc" class="item" tag="div">文件选择</router-link>
				<router-link to="/vote" class="item" tag="div">上传</router-link>
			</nav>
			<div class="content col-md-12">
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'app',
		beforeCreate () {
			this.$store.commit('readAccList')
		}
	}
</script>

<style lang="stylus" scoped>
	nav{
		display: flex
		text-align center
		.item{
			flex:1
			height:40px
			line-height:40px
			background-color #f5c607
			color: #fff
			
			&.active{
				background-color #fff
				color: #000
			}
		}
	}
</style>



